* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  background: rgb(18, 54, 9) url('../src/imgs/bg.jpg') no-repeat center center/cover;
  height: 100%;
  color: rgb(226, 231, 227);
  font-family: Arial, Helvetica, sans-serif;
  background-attachment: fixed;
  position: relative;
}


a {
  text-decoration: none;
}

.container {
  max-width: 1100px;
  margin: auto;
  padding: 0 20px;
}

.btn {
  display: inline-block;
  color: #fff;
  background-color: #3fb573;
  font-size: 1em;
  text-align: center;
  padding: 10px 15px;
  border: 0;
  margin: 10px 0;
}

header {
  height: 200px;
}

header img {
  width: 200px;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.search {
  height: 100px;
}


input[type='text'] {
  display: block;
  padding: 10px;
  font-size: 20px;
  border: none;
  border-radius: 5px;
  width: 60%;
  margin: auto;
  background-color: rgb(61, 63, 61);
  color: white;
}


.cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
}

.card {
  cursor: pointer;
  background-color: transparent;
  height: 300px;
  perspective: 1000px;
}

.card h1 {
  font-size: 25px;
  border-bottom: 1px #fff solid;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.card img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.card:hover .card-inner {
  transform: rotateY(180deg);
}

.card-front,
.card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.card-back {
  background-color: #333;
  color: white;
  padding: 20px;
  transform: rotateY(180deg);
}

.card li {
  list-style: none;
  padding-bottom: 10px;
}
.text{
  background-color: rgba(14, 126, 73, 0.719);
  height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  margin-bottom: 20px;
  border-radius: 15px;
  color: rgb(240, 233, 233);
  padding:10px;
}
.footer{
  height: 5vh;
  background-color: rgba(51, 51, 51, 0.452);
  text-align: center;
  margin-top: 10px;
  padding-top: 20px;
  letter-spacing: 2px;
  color: #e9fa02;
  /* position: fixed;
  bottom: 0;
  width: 100%; */
  bottom: 0;
}


@media (max-width: 800px) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
  
}

@media (max-width: 500px) {
  .cards {
    grid-template-columns: 1fr;
  }
  input[type='text'] {
    width: 100%;
  }
  .card-front,
.card-back {
  position: absolute;
  width: 100%;
  height: 400px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
  .card img {
    width: 100%;
    height: 400px;
    object-fit: cover;
  }
 
}
